* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 75px;
  background-color: #00828c;
  color: white;
  position: sticky;
  top: 0;
  z-index: 999;
}

.logo {
  font-size: 30px;
}

.logoHome {
  text-decoration: none;
  color: white;
}

.nav-links {
  display: flex;
  justify-content: space-around;
  list-style: none;
  width: 75%;
}

.home,
.popular,
.about {
  text-decoration: none;
  color: white;
  font-size: 20px;
  padding: 10px 20px;
  margin: 0px 10px;
  cursor: pointer;
}

.mobile-menu-icon {
  display: none;
}

@media screen and (max-width: 780px) {
  .logo {
    font-size: 24px;
  }

  .nav-links {
    display: none;
  }

  .home,
  .about,
  .popular {
    color: white;
    text-align: center;
    padding: 32px;
    width: 100%;
    transition: all 0.5s ease;
  }

  .nav-links-mobile {
    position: absolute;
    display: block;
    list-style: none;
    background-color: #005469;
    left: 0;
    top: 75px;
    transition: all 0.5s ease-out;
    width: 100%;
    z-index: 999;
  }

  .mobile-menu-icon {
    display: block;
    position: absolute;
    font-size: 30px;
    color: white;
    background-color: #00828c;
    border: none;
    outline: none;
    top: 15px;
    right: 25px;
    cursor: pointer;
  }
}
